* {
    box-sizing: border-box;
}


.navigation-sub {
    background-color: rgba(0, 134, 134, 0.52);
    width: 100%;
    height: 35px;
    box-sizing: border-box;
    top: 0;
    display: block;
    position: fixed;
}

.item{
    color: black;
    font-size: 2.5ch;
    margin-left: 90px;

    
}

.item:hover {
    color: rgb(0, 18, 98);
}
#nav_item{
    box-sizing: border-box;
    /* color: white; */
    list-style-type: none;
}

li:hover:not(.li) {
    font-size: 2ch;
    color: black;
}

a:link {
    text-decoration: none;
  }

img {
    margin: 5px;
    float: left;
    width: 180px;
}
